<template>
  <div class="min-h-screen flex flex-col bg-white">
    <!-- 상단 네비게이션 -->
    <div class="flex items-center p-4 mt-10">
      <button @click="goBack" class="mr-2 text-gray-600 hover:text-gray-900">
        <svg
          xmlns="http://www.w3.org/2000/svg"
          class="h-8 w-8"
          fill="none"
          viewBox="0 0 24 24"
          stroke="currentColor"
        >
          <path
            stroke-linecap="round"
            stroke-linejoin="round"
            stroke-width="2"
            d="M15 19l-7-7 7-7"
          />
        </svg>
      </button>
    </div>

    <!-- 본문 (입력 영역) -->
    <div class="flex-1 px-6">
      <h3 class="text-gray-800 font-semibold mb-6 text-2xl">
        이름과 휴대폰 번호를<br />입력해 주세요.
      </h3>

      <!-- 이름 입력 -->
      <div class="mt-20 mb-4 relative">
        <input
          type="text"
          v-model="userName"
          placeholder="이름"
          class="w-full px-4 py-3 pr-10 border rounded-md focus:outline-none focus:ring-2 focus:ring-green-600"
        />
        <button
          v-show="userName"
          @click="userName = ''"
          type="button"
          class="absolute top-1/2 right-3 -translate-y-1/2 text-gray-400 hover:text-gray-600"
        >
          ✕
        </button>
      </div>

      <!-- 휴대폰 번호 입력 -->
      <div class="mb-6 relative">
        <input
          type="tel"
          v-model="phoneNumber"
          placeholder="휴대폰 번호"
          class="w-full px-4 py-3 pr-10 border rounded-md focus:outline-none focus:ring-2 focus:ring-green-600"
        />
        <button
          v-show="phoneNumber"
          @click="phoneNumber = ''"
          type="button"
          class="absolute top-1/2 right-3 -translate-y-1/2 text-gray-400 hover:text-gray-600"
        >
          ✕
        </button>
      </div>
    </div>

    <!-- 하단 버튼 영역 -->
    <div class="p-6">
      <button
        @click="sendCode"
        class="w-full bg-green-700 text-white py-3 rounded-md hover:bg-green-800 transition"
      >
        인증번호 발송
      </button>
    </div>
  </div>
</template>

<script setup>
import { ref } from 'vue'
import { useRouter } from 'vue-router'

const router = useRouter()
const userName = ref('')
const phoneNumber = ref('')

// 뒤로가기 (로그인 화면으로 이동)
const goBack = () => {
  router.back()
}

// 인증번호 발송 로직
const sendCode = () => {
  if (!userName.value || !phoneNumber.value) {
    alert('이름과 휴대폰 번호를 입력해 주세요.')
    return
  }
  alert('인증번호가 발송되었습니다!')
}
</script>
